.header{background-color:rgba(77,44,94,.7333333333);padding:10px 0;position:fixed;top:0;left:0;right:0;z-index:100;width:100%}.header .content{display:flex;align-items:center;justify-content:space-between;padding:0 15px;max-width:1200px;margin:0 auto}.header .hamburger{font-size:1.5rem;cursor:pointer;color:#e7e5e5;display:block}.header .logo img{width:37px;height:37px;object-fit:contain}.header .nav{display:none;position:absolute;top:100%;left:0;right:0;background-color:#5848d1}.header .nav.active{display:block}.header .nav ul{list-style:none;padding:0;margin:0}.header .nav ul li{border-bottom:1px solid rgba(255,255,255,.1)}.header .nav ul li:last-child{border-bottom:none}.header .nav ul li a{display:block;padding:15px;color:#fff;text-decoration:none;font-weight:500;transition:background-color .3s ease}.header .nav ul li a:hover{background-color:rgba(255,255,255,.1)}.header .rightSection{display:flex;align-items:center}.header .search{display:none;align-items:center;margin-right:15px}.header .search input{padding:10px;border:2px solid #fff;border-top-left-radius:12px;border-bottom-left-radius:12px;border-right:none;font-size:14px;background:rgba(255,255,255,.4666666667);outline:none}.header .search input:active,.header .search input:focus{background:rgba(0,0,0,0);color:#fff}.header .search input::placeholder{color:#fff}.header .search .searchBtn{background-color:rgba(255,255,255,.4666666667);border:2px solid #fff;border-top-right-radius:12px;border-bottom-right-radius:12px;border-left:none;padding:11px 18px;cursor:pointer;font-size:14px;display:flex;align-items:center;justify-content:center;color:rgba(77,44,94,.7333333333)}.header .language{display:flex;align-items:center;margin-right:15px}.header .language .bold{background:#fff;border-radius:5px;border:none;color:#000;font-weight:500;cursor:pointer;padding:5px 10px}.header .language .normal{background:none;border:none;color:#fff;font-weight:500;cursor:pointer;padding:5px 10px;transition:opacity .3s ease}.header .auth button{background-color:#4d2c5e;color:#fff;border:none;padding:8px 15px;border-radius:4px;cursor:pointer;font-weight:500;transition:opacity .3s ease}.header .auth button:hover{opacity:.8}@media(min-width: 768px){.header .hamburger{display:none}.header .nav{display:block;position:static;background-color:rgba(0,0,0,0)}.header .nav ul{display:flex}.header .nav ul li{border-bottom:none;margin-right:20px}.header .nav ul li:last-child{margin-right:0}.header .nav ul li a{padding:0}.header .nav ul li a:hover{background-color:rgba(0,0,0,0);opacity:.8}.header .search{display:flex}.header .auth{display:block}}@media(max-width: 767px){.header .content{padding:0 10px}.header .search{display:none}.header .nav{display:none}.header .nav.active{display:block}}@media(max-width: 430px){.header{position:absolute;background:rgba(0,0,0,0);top:10}.header .nav{background:rgba(139,84,167,.4784313725)}.header .content{align-items:center}.header .logo{display:none}.header .language{background:rgba(139,84,167,.4784313725);border:1px solid #4d2c5e;color:#fff;border-radius:8px;padding:8px 14px}.header .auth button{padding:14px 24px;border-radius:10px}.header .hamburger{font-size:2rem}.header .search{display:none}}@media(max-width: 320px){.header{position:absolute;background:rgba(0,0,0,0)}.header .nav a{font-size:14px}.header .hamburger{font-size:1.8rem}.header .logo{display:none}.header .auth button{padding:8px 16px;border-radius:10px}.header .language{background:rgba(139,84,167,.4784313725);border:1px solid #4d2c5e;color:#fff;border-radius:8px;padding:2px 8px}.header .search{display:none}}@media(min-width: 1024px){.header .content{justify-content:flex-start}.header .logo{margin-right:30px}.header .nav{margin-right:auto}.header .rightSection{margin-left:auto}}